<template>
  <div class="goodslist-item"  @click="itemClick">
    <img v-lazy="showImg" @load="imgload">
    <div class="goods-info">
      <p>{{goodsItem.title}}</p>
      <span class="price">￥{{goodsItem.price}}</span>
      <span class="collect">{{goodsItem.cfav}}</span>
    </div>
  </div>
</template>
<script>
export default {
  name: 'GoodsListItem',
  components: {},
  data() {
    return {
    }
  },
  props: {
    goodsItem: {
      type: Object,
      default(){
        return {}
      }
    }
  },
  computed: {
    showImg(){
      return this.goodsItem.image || this.goodsItem.show.img
    }
  },
  methods: {
    imgload(){
      this.$bus.$emit('itemImgLoad')
    },
    itemClick(){
      this.$router.push({
        path: '/detail',
        query: {
          id: this.goodsItem.iid
        }
      })
      .catch(err => {
      console.log('all good')
      })
    }
  }
}
</script>
<style scoped>
.goodslist-item {
  width: 48%;
  padding: 5px 0;
  text-align: center;
}
.goodslist-item img {
  width: 100%;
  border-radius: 5px;
}

.goodslist-item p {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  padding-bottom: 3px;
}
.collect {
  position: relative;
}
.price {
  color: var(--color-high-text);
  margin-right: 25px;
}

.collect::before {
  content: '';
  
  position: absolute;
  top: 0;
  left: -15px;
  width: 14px;
  height: 14px;

  background: url("~assets/img/common/collect.svg") 0 0/14px 14px;
}
</style>